*{
  text-decoration: none;
  list-style: none;
  color: rgb(5, 5, 156);
  font-family: "Cafe";
}
body{
  width: 1000px;
  border: 8px dashed blue;
  margin: 0 auto;
}
@font-face {
  font-family: "Cafe";
  font-weight: 400;
  src: url("../public/fonts/Cafe24Dangdanghae.ttf")
}

ul {
  font-size: 2em;
}
h1 {
  text-align: center;
  font-size: 3.8em;
  color: rgb(5, 5, 236);
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}
#actTitleUl {
  display: flex;
  width: 920px;
  justify-content: space-between;
}
.actTtitle {
  width: 140px;
  flex-direction: row;
}
#actIdUl{
  width: 900px;
  height: 130px;
  margin-top: 40px;
}
#actId{
  background-color: rgba(0, 227, 110, 0.753);
  width: 160px;
  height: 100px;
  margin-bottom: 13px;
  border-radius: 70px;
  box-shadow: 1px 2px 1px #ccc;
  margin-right: 20px;
  float: left;
  position: relative;
  font-size: 0.72em;
}
#actId:hover {
  background-color: rgb(0, 0, 212);

}
.actDiv{
  width: 140px;
  height: 120px;
  position: absolute;
  top: 27px;
  left: 13px;
  text-align: center;
}
.actDiv:hover{
  color: rgb(30, 255, 139);
}
.active{
  background-color: rgb(1, 1, 161);
  color: white;
}
#actClickDiv{
  position: relative;
  width: 100%;
  height: 600px;
}
#actTitleH3{
  text-align: center;
  font-size: 1.8em;
}
#actP {
  text-align: center;
  font-style: italic;
  font-size: 1.2em;
}
.activityImage {
  width: 640px;
  height: 460px;
  box-shadow: 4px 4px 2px rgb(130, 130, 130);
  position: absolute;
  left: 180px;
}
.islandImage {
  width: 640px;
  height: 460px;
  box-shadow: 4px 4px 2px rgb(130, 130, 130);
  position: absolute;
  left: 180px;
}
#islandUl {
  display: flex;
  width: 920px;
  justify-content: space-between;
}
.islandTtitle {
  width: 140px;
  flex-direction: row;
}
#islandIdUl{
  width: 900px;
  height: 130px;
  margin-top: 40px;
}
#islandId{
  background-color: rgba(0, 227, 110, 0.753);
  width: 160px;
  height: 100px;
  margin-bottom: 13px;
  border-radius: 70px;
  box-shadow: 1px 2px 1px #ccc;
  margin-right: 20px;
  float: left;
  position: relative;
  font-size: 0.72em;
}
#islandId:hover {
  background-color: rgb(0, 0, 212);

}
.islandDiv{
  width: 140px;
  height: 120px;
  position: absolute;
  top: 38px;
  left: 13px;
  text-align: center;
}
.islandDiv:hover{
  color: rgb(30, 255, 139);
}

#islandClickDiv{
  position: relative;
  width: 100%;
  height: 600px;
}
#islandTitleH3{
  text-align: center;
  font-size: 1.8em;
}
#islandP {
  text-align: center;
  font-style: italic;
  font-size: 1.2em;
}
#MainDiv {
  width: 100%;
  height: 700px;
  background:linear-gradient(#fff 64%, rgb(60, 60, 255) 100%);
  padding-top: 5px;
  position: relative;
}
#hawaiMainTitle{
  font-size: 3em;
  text-align: center;

}
#hawaiMainImage {
  width: 640px;
  height: 460px;
  box-shadow: 4px 4px 2px rgb(130, 130, 130);
  position: absolute;
  left: 180px;
}
#hawaiMainDesc{
  text-align: center;
  font-size: 1.5em;
  font-style: oblique;
}